import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponentComponent } from './about-component/about-component.component';
import { AwardComponentComponent } from './award-component/award-component.component';
import { EducationComponentComponent } from './education-component/education-component.component';
import { ExperienceComponentComponent } from './experience-component/experience-component.component';
import { InterestComponentComponent } from './interest-component/interest-component.component';
import { SkillComponentComponent } from './skill-component/skill-component.component';

const routes: Routes = [
  { path: '', component: AboutComponentComponent },
  { path: 'Awards', component: AwardComponentComponent },
  { path: 'Education', component: EducationComponentComponent },
  { path: 'Experience', component: ExperienceComponentComponent },
  { path: 'Interests', component: InterestComponentComponent },
  { path: 'Skills', component: SkillComponentComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
